<template>
  <div>
    <heder></heder>
    <broad></broad>
    <div class="content">
      <el-button  @click="centerDialogVisible = true" >商品片添加</el-button>
      <template>
        <el-table
          :data="tableData3"
          style="width: 100%"
          height="250">
          <el-table-column
            fixed
            prop="goodsId"
            label="ID"
            width="150">
          </el-table-column>
          <el-table-column
            prop="goodsPictureUrl"
            label="图片链接"
            width="120">
          </el-table-column>
          <el-table-column
            prop="description"
            label="描述"
            width="120">
          </el-table-column>


        </el-table>
      </template>

      <!--商品片添加-->
      <el-dialog
        title="提示"
        :visible.sync="centerDialogVisible"
        width="30%"
        center>
        <el-radio-group v-model="labelPosition" size="small">
          <el-radio-button label="left">左对齐</el-radio-button>
          <el-radio-button label="right">右对齐</el-radio-button>
          <el-radio-button label="top">顶部对齐</el-radio-button>
        </el-radio-group>
        <div style="margin: 20px;"></div>
        <el-form :label-position="labelPosition" label-width="80px" :model="formLabelAlign">
          <el-form-item label="商品图片">
            <el-input v-model="formLabelAlign.name"></el-input>
          </el-form-item>
          <el-form-item label="图片链接">
            <el-input v-model="formLabelAlign.region"></el-input>
          </el-form-item>
          <el-form-item label="是否有效">
            <el-input v-model="formLabelAlign.type"></el-input>
          </el-form-item>
          <el-form-item label="图片类型">
            <el-input v-model="formLabelAlign.typ"></el-input>
          </el-form-item>
        </el-form>
        <span slot="footer" class="dialog-footer">
    <el-button @click="centerDialogVisible = false">取 消</el-button>
           <span @click="centerDialogVisible = false">
             <el-button type="primary" @click="bresave()">确 定</el-button>
          </span>
  </span>
      </el-dialog>
    </div>
  </div>
</template>

<script>
  import {Afragment,Panlie} from '../../axios/ajax/ajax'
  import heder from '@/components/heder'
  import broad from '@/components/broadside'


 const ai={
    name: '',
      region: '',
      type: '',
      typ:""
  }
  export default {
    name: 'xxx',
    data(){
      return {
        centerDialogVisible: false,

        labelPosition: 'right',
        formLabelAlign: {
          name: '',
          region: '',
          type: '',
          typ:""
        },
        tableData3: [

        ]
      }
    },
    created(){
      let that = this;
     var Id=that.$route.params.id

      Panlie({goodsId:Id
      }).then( (res)=>{
        if(res.code ===200){
this.tableData3=res.data.list
        }
      })


    },
    mounted(){


    },
    methods: {
      init(a,b){
        for(let i in a){
          a[i] = b[i]
        }
      },
      bresave(){

        Afragment({goodsId:this.formLabelAlign.name,goodsPictureUrl:this.formLabelAlign.region,
          isVaild:this.formLabelAlign.type, type:this.formLabelAlign.typ,
        }).then((res) => {

          if(res.code==200){

            this.init(this.formLabelAlign,ai)

          }
        });
      }
    },
    components: {
      heder,
      broad
    }

  }
</script>

<style lang="scss" scoped>
  .el-table--border{
    margin-top: 20px;
  }
  .content {
    background: none repeat scroll 0 0 #fff;
    position: absolute;
    left: 210px;
    right: 0;
    top: 70px;
    bottom: 0;
    width: auto;
    padding: 40px;
    box-sizing: border-box;
    overflow-y: scroll;
  }
</style>




