<template>
<div>
  <heder></heder>
  <broad></broad>
  <div class="content">


    <el-button type="primary">查询</el-button>
    <template>
      <el-table
        :data="tableData"
        border
        style="width: 100%">
        <el-table-column
          prop="shopName"
          label="店铺名"
          width="180">
        </el-table-column>
        <el-table-column
          prop="phone"
          label="手机号"
          width="180">
        </el-table-column>

        <el-table-column
          label="操作"
        >
          <template slot-scope="scope">
            <!--<span @click="centerDialogVisible = true" ></span>-->
            <el-button type="primary" size="small"  @click="update(scope.row)" v-text="TOguo==false? '未审核':'已通过'"></el-button>

            <!--<el-button type="danger" size="small" @click="del(scope.row.id)">删除</el-button>-->
          </template>
        </el-table-column>

      </el-table>
    </template>
      </div>
</div>
</template>

<script>
  import heder from '@/components/heder'
  import broad from '@/components/broadside'
  import {Blie,Sdanb} from '../../axios/ajax/ajax'

    export default {
        name: 'xxx',
        data(){
            return {
              tableData:[]
              ,TOguo:false

            }
        },
        mounted(){
        },
      created(){
        Blie({}).then((res) => {

          if(res.code==200){

            this.tableData=res.data.list
            console.log(this.tableData);
          }

        });

      },
        methods: {
          update(OS){
              const status=0
            Sdanb({agentId:OS.agentId,status:status,agentName:OS.agentName,idAccount:OS.idAccount}).then((res) => {

              if(res.code==200){
                this.TOguo=true
              }

            });

          }
        },
        components: {
          heder,
          broad
        }

    }
</script>

<style lang="scss" scoped>
  .content {
    background: none repeat scroll 0 0 #fff;
    position: absolute;
    left: 210px;
    right: 0;
    top: 70px;
    bottom: 0;
    width: auto;
    padding: 40px;
    box-sizing: border-box;
    overflow-y: scroll;
  }
</style>



